import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { errorMessage } from 'src/app/messages';
import { CommonService } from 'src/app/service/common.service';
import { CommonModuleService } from 'src/app/service/common-module.service';
declare var swal: any;


@Component({
  selector: 'app-table-view',
  templateUrl: './table-view.component.html',
  styleUrls: ['./table-view.component.css']
})
export class TableViewComponent implements OnInit {

  @Input() tableConfig;
  @Output() onChange = new EventEmitter()



  TableHead: any;
  TableValue: any;
  TableActionAddLink: any;
  showActions: any;
  tableDataLink: any;
  actionButton: any

  constructor(
    private commonModuleService: CommonModuleService
  ) {

  }

  ngOnInit() {
    this.TableHead = this.tableConfig.TableHead
    this.TableValue = this.tableConfig.TableValue
    this.showActions = this.tableConfig.showActions
  }
  ngOnDestroy() {

  }

  deleteObject(id, url) {
    swal.fire({
      title: errorMessage.delete_header_text,
      text: errorMessage.delete_smalll_text,
      icon: errorMessage.delete_dialogue_type,
      showCancelButton: true,
      confirmButtonText: errorMessage.delete_confirm_button,
      cancelButtonText: errorMessage.delete_cancel_button,
    }).then((result) => {
      if (result.value) {
        this.commonModuleService.delete(id, url).subscribe(
          (response) => {
            if (response.success) {
              this.onChange.emit(true);
            }
          },
        );
      }
    }).catch(swal.noop);
  }

}

